// PACKAGES
import React from 'react'
import styled, {css, keyframes} from 'styled-components'
// HTML
import html from '../../../Images/experience/html/html.svg'
import gmail from '../../../Images/experience/html/gmail.svg'
// CSS
import responsive from '../../../Images/experience/css/responsive.svg'
import noBootstrap from '../../../Images/experience/css/noBootstrap.svg'
import sass from '../../../Images/experience/css/sass.svg'
import sc from '../../../Images/experience/css/sc.svg'
// JS
import es6 from '../../../Images/experience/js/es6.svg'
import noJQuery from '../../../Images/experience/js/noJQuery.svg'
import react from '../../../Images/experience/js/react.svg'
// FIREBASE
import db from '../../../Images/experience/firebase/db.svg'
import au from '../../../Images/experience/firebase/au.svg'
import st from '../../../Images/experience/firebase/st.svg'
import cf from '../../../Images/experience/firebase/cf.svg'
import fh from '../../../Images/experience/firebase/fh.svg'
// NODEJS
import npm from '../../../Images/experience/nodejs/npm.svg'
import express from '../../../Images/experience/nodejs/express.svg'
import mongodb from '../../../Images/experience/nodejs/mongodb.svg'
import pug from '../../../Images/experience/nodejs/pug.svg'
// PYTHON
import terminal from '../../../Images/experience/python/terminal.svg'
// OFFICE
import xls from '../../../Images/experience/office/excel.svg'
import ppt from '../../../Images/experience/office/powerpoint.svg'
import doc from '../../../Images/experience/office/word.svg'
// C++

const fadeIn = keyframes`
	from {
		opacity: 0;
		transform: scale(0.9)
	}
	to {
		opacity: 1;
		transform: scale(1);
	}
`
const fadeOut = keyframes`
	from {
		opacity: 1;
		transform: scale(1);
	}
	to {
		opacity: 0;
		transform: scale(0.9)
	}
`
const ExpImage = styled.img`
	width: 100px;
	height: 100px;
	margin: 50px 0 20px 0;
	animation: ${fadeIn} 0.3s both;
	${props =>
		props.hide &&
		css`
			animation: ${fadeOut} 0.3s both;
		`
	}
	${props =>
		props.radius &&
		css`
			border-radius: 10px;
		`
	};
`
const ExpDescription = styled.div`
	width: 100%;
	animation: ${fadeIn} 0.3s both;
	${props =>
		props.hide &&
		css`
			animation: ${fadeOut} 0.1s both;
		`
	};
`
const BrowserLink = styled.a`
	color: var(--color-main);
	text-decoration: none;
	font-weight: bold;
	transition: opacity 0.2s ease;
	:hover {
		opacity: 0.5;
	}
`

const contents = (frontendExp, backendExp, otherExp) => {
	return {
		html: <>
			<ExpImage hide={frontendExp.hide} src={html} />
			<ExpDescription hide={frontendExp.hide}>
				Tworz strony, oraz aplikacje internetowe zgodne z najnowszym standardem <BrowserLink href="https://developer.mozilla.org/pl/docs/HTML/HTML5" target="_blank" rel="noopener noreferrer">HTML5</BrowserLink>.
				Tworzone przeze mnie projekty s w peni semantyczne, a co za tym idzie nowoczesne i lepiej wsppracuj z innymi urzdzeniami.
			</ExpDescription>
			<ExpImage src={gmail} />
			<ExpDescription hide={frontendExp.hide}>
				Mam dowiadczenie z tworzeniem automatycznych szablonw e-mail.
				Swoje usugi zaoferowaem ju midzy innymi firmie <BrowserLink href="https://magicalgoods.com.pl" target="_blank" rel="noopener noreferrer">Magical Goods</BrowserLink>.
			</ExpDescription>
		</>,
		css: <>
			<ExpImage hide={frontendExp.hide} src={responsive} />
			<ExpDescription hide={frontendExp.hide}>
				Wszystkie moje projekty s w peni <BrowserLink href="https://pl.wikipedia.org/wiki/Responsive_web_design" target="_blank" rel="noopener noreferrer">responsywne</BrowserLink>.
				Podczas ich tworzenia kieruj si zasad <BrowserLink href="https://en.ryte.com/wiki/Mobile_First" target="_blank" rel="noopener noreferrer">Mobile First</BrowserLink>, dziki czemu korzystanie ze strony jest wygodne na wszystkich urzdzeniach, a tre na ekranach sabszych urzdze aduje si znacznie szybciej.
			</ExpDescription>
			<ExpImage hide={frontendExp.hide} src={noBootstrap} />
			<ExpDescription hide={frontendExp.hide}>
				Do kadego klienta podchodz indywidualnie.
				Jestem przeciwnikiem korzystania z gotowych <BrowserLink href="https://pl.wikipedia.org/wiki/Framework" target="_blank" rel="noopener noreferrer">frameworkw</BrowserLink> <BrowserLink href="https://pl.wikipedia.org/wiki/Kaskadowe_arkusze_styl%C3%B3w" target="_blank" rel="noopener noreferrer">CSS</BrowserLink> takich jak <BrowserLink href="https://getbootstrap.com" target="_blank" rel="noopener noreferrer">Bootstrap</BrowserLink> czy <BrowserLink href="https://get.foundation" target="_blank" rel="noopener noreferrer">Foundation</BrowserLink>.
				Uwaam, e dopracowany na stronie powinien by kady szczeg, a zainwestowany czas zwrci si nam z nawizk.
			</ExpDescription>
			<ExpImage hide={frontendExp.hide} src={sass} />
			<ExpDescription hide={frontendExp.hide}>
				Czysto kodu to podstawa!
				Nawet <BrowserLink href="https://pl.wikipedia.org/wiki/Kaskadowe_arkusze_styl%C3%B3w" target="_blank" rel="noopener noreferrer">CSS</BrowserLink>.
				Dlatego korzystam z preprocesorw takich jak <BrowserLink href="https://sass-lang.com" target="_blank" rel="noopener noreferrer">SCSS, czy SASS</BrowserLink>, aby ewentualne poprawki mc szybko zastosowa, a potencjalne przysze osoby potrafiy odnale si w kodzie.
			</ExpDescription>
			<ExpImage hide={frontendExp.hide} src={sc} />
			<ExpDescription hide={frontendExp.hide}>
				Wikszo styli moich projektw oparte jest na <BrowserLink href="https://styled-components.com" target="_blank" rel="noopener noreferrer">Styled Components</BrowserLink>.
				Technologia ta daje moliwo dokadniejszego zarzdzania elementem i co bardzo sobie ceni - indywidualnego podejcia.
				Tak jak ju wyej wspomniaem, na stronie lubi mie dopracowany kady, nawet najmniejszy szczeg.
			</ExpDescription>
		</>,
		js: <>
			<ExpImage hide={frontendExp.hide} src={es6} radius />
			<ExpDescription hide={frontendExp.hide}>
				Moje mae dziea sztuki staram si tworzy tak, aby byy nowoczesne, a kod czytelny.
				Dlatego korzystam z funkcji JavaScript w standardzie ES6+.
				Jednoczenie chc dotrze do jak najwikszej liczby odbiorcw, dlatego z pomoc takich narzdzi jak <BrowserLink href="https://babeljs.io" target="_blank" rel="noopener noreferrer">Babel</BrowserLink> moje strony obsugiwane s poprawnie przez wikszo przegldarek.
			</ExpDescription>
			<ExpImage hide={frontendExp.hide} src={react} />
			<ExpDescription hide={frontendExp.hide}>
				Zdecydowana wikszo moich aplikacji napisana jest w <BrowserLink href="https://pl.reactjs.org" target="_blank" rel="noopener noreferrer">React</BrowserLink>.
				Porzdek i struktura przy wikszych projektach jest bardzo wana.
				Tak Moliwo daje mi wanie biblioteka <BrowserLink href="https://pl.reactjs.org" target="_blank" rel="noopener noreferrer">React.js</BrowserLink> z ktr styczno mam ju prawie dwa lata.
			</ExpDescription>
			<ExpImage hide={frontendExp.hide} src={noJQuery} />
			<ExpDescription hide={frontendExp.hide}>
				Nie "uatwiam" sobie pisania kodu przestarzaymi bibliotekami.
				Stawiam przede wszystkim na nowoczesno i szybko dziaania.
				Dlatego te nie uywam biblioteki <BrowserLink href="https://jquery.com" target="_blank" rel="noopener noreferrer">jQuery</BrowserLink>, a bardziej staram si zagbia w wiat czystego JavaScript'u.
			</ExpDescription>
		</>,
		firebase: <>
			<ExpImage hide={backendExp.hide} src={db} />
			<ExpDescription hide={backendExp.hide}>
			Z pomoc usugi <BrowserLink href="https://firebase.google.com/docs/firestore" target="_blank" rel="noopener noreferrer">Firebase Firestore</BrowserLink> potrafi stworzy strony z edytowaln zawartoci.
			Od teraz nie musisz prosi programisty o kad poprawk, bo wybrane rzeczy bdziesz w stanie zrobic samemu.
			Dokadnie!
			Zrobi dla ciebie stron, ktr w prosty sposb i w kadej chwili bdziesz mg dostosowa wedle uznania.
			</ExpDescription>
			<ExpImage hide={backendExp.hide} src={au} />
			<ExpDescription hide={backendExp.hide}>
				Potrzebujesz systemu zalogowanych uytkownikw?
				Nie ma sprawy.
				Z uyciem <BrowserLink href="https://firebase.google.com/docs/auth" target="_blank" rel="noopener noreferrer">Firebase Authentication</BrowserLink> jestem w stanie zrobi dla ciebie system logowania na stronie, dziki czemu kady uytkownik twojej strony bdzie mg tworzy i edytowa wasne dane.
				Rejestracja moe by dostpna bezporednio z twojej strony (za spraw poczty e-mail lub numeru telefonu), jak rwnie z pomoc wszystkich najpopularniejszych Social Media takich jak:&nbsp;
				<BrowserLink href="https://pl.wikipedia.org/wiki/Google%2B" target="_blank" rel="noopener noreferrer">Google+</BrowserLink>,&nbsp;
				<BrowserLink href="https://www.facebook.com" target="_blank" rel="noopener noreferrer">Facebook</BrowserLink>,&nbsp;
				<BrowserLink href="https://twitter.com/explore" target="_blank" rel="noopener noreferrer">Twitter</BrowserLink>, czy&nbsp;
				<BrowserLink href="https://github.com" target="_blank" rel="noopener noreferrer">GitHub</BrowserLink>.
			</ExpDescription>
			<ExpImage hide={backendExp.hide} src={st} />
			<ExpDescription hide={backendExp.hide}>
				A co jeli chcesz przesa do swojej strony nie tylko dane tekstowe, lecz rwnie zdjcia i filmy?
				To rwnie moge dla ciebie zrobi!
				Pozwala mi na to usuga <BrowserLink href="https://firebase.google.com/docs/storage" target="_blank" rel="noopener noreferrer">Firebase Storage</BrowserLink>, dziki czemu na swojej stronie bdziesz mg rwnie "przechowywa" pliki multimedialne.
			</ExpDescription>
			<ExpImage hide={backendExp.hide} src={cf} />
			<ExpDescription hide={backendExp.hide}>
				Potrafi sie rwnie posugiwa <BrowserLink href="https://firebase.google.com/docs/functions" target="_blank" rel="noopener noreferrer">Cloud Functions</BrowserLink>, ktre pozwol ci na zarzdzanie bardziej skomplikowanymi sprawami na stronie.
				Daj moliwo midzy innymi dodawania administratorw na stronie, dziki czemu jeszcze sprawniej i co waniejsze BEZPIECZNIEJ bdziesz mg zarzdza uytkownikami na stronie.
			</ExpDescription>
			<ExpImage hide={backendExp.hide} src={fh} />
			<ExpDescription hide={backendExp.hide}>
				Korzystajc z <BrowserLink href="https://firebase.google.com/docs/hosting" target="_blank" rel="noopener noreferrer">Firebase Hosting</BrowserLink> jestem w stanie opublikowa twoj stron zupenie za darmo.
				Zgadza si!
				Usuga <BrowserLink href="https://firebase.google.com/docs/hosting" target="_blank" rel="noopener noreferrer">Firebase Hosting</BrowserLink> jest zupenie darmowa, wic jeli pki co nie sta ci na kupno hostingu i domeny dla swojej strony, jestem w stanie zaatwi ci te rzeczy za darmo.
				Dodatkowo wspomn, e z pomoca narzdzi <BrowserLink href="https://analytics.google.com" target="_blank" rel="noopener noreferrer">Google Analytics</BrowserLink> sprawdzisz jaki ruch w internecie generuje twoja strona.
				Dziki temu na bieco bdziesz mie moliwo monitorowania zmian, a w przyszoci nawet zarabiania!
			</ExpDescription>
		</>,
		nodejs: <>
			<ExpImage hide={backendExp.hide} src={npm} />
			<ExpDescription hide={backendExp.hide}>
				Mimo, e programowaniem zajmuj si ju prawie 3 lata i mogoby to si wydawa do sporo, cigle ucz si czego nowego.
				Ciekawo i cige odkrywanie nowych rzeczy zapewnia mi <BrowserLink href="https://www.npmjs.com" target="_blank" rel="noopener noreferrer">npm</BrowserLink> (Node Package manager) z ogromn iloci paczek.
				Potrafi posugiwac si dokumentacj, instalowa konkretne wersje paczek, jak i tworzy wasne.
				Z jednej strony uatwiaja mi to bardzo prac, z drugiej za stanowi wyzwanie, przez co nie mam czasu na nud!
			</ExpDescription>
			<ExpImage hide={backendExp.hide} src={mongodb} />
			<ExpDescription hide={backendExp.hide}>
				Potrafi posugiwa si <BrowserLink href="https://mansfeld.pl/bazy-danych/bazy-danych-nosql-zalety-wady" target="_blank" rel="noopener noreferrer">nierelacyjn</BrowserLink> baz danych <BrowserLink href="https://www.mongodb.com" target="_blank" rel="noopener noreferrer">MongoDB</BrowserLink>.
				Umiem zapisywa, edytowa i usuwa dane w bazie, oraz tworzy warunki.
			</ExpDescription>
			<ExpImage hide={backendExp.hide} src={express} />
			<ExpDescription hide={backendExp.hide}>
				Znam podstawy frameworku <BrowserLink href="https://expressjs.com" target="_blank" rel="noopener noreferrer">Express.js</BrowserLink>, dziki czemu jestem w stanie stworzy prosty serwer.
				Potrawi stworzy tzw. routing (podstrony) aplikacji, zintegrowa j z rnymi silnikami do generowania widokw itp.
				Pki co cay czas ucz si tej technologii i wikszo swojej wiedzy posiadam z <BrowserLink href="https://www.udemy.com/course/nodejs-master-class" target="_blank" rel="noopener noreferrer">kursu</BrowserLink> Mosh'a Hamedani na <BrowserLink href="https://www.udemy.com" target="_blank" rel="noopener noreferrer">Udemy</BrowserLink>.
			</ExpDescription>
			<ExpImage hide={backendExp.hide} src={pug} />
			<ExpDescription hide={backendExp.hide}>
				Jednym z generatorw widoku o ktrych wspomniaem wczeniej jest <BrowserLink href="https://pugjs.org" target="_blank" rel="noopener noreferrer">Pug.js</BrowserLink>.
				To technologia, ktra pozwala mi z jednej strony na stworzenie widoku strony, z drugiej za pozwala na pisanie czystszego kodu.
			</ExpDescription>
		</>,
		py: <>
			<ExpImage hide={otherExp.hide} src={terminal} />
			<ExpDescription hide={otherExp.hide}>
				W jzyku <BrowserLink href="https://www.python.org" target="_blank" rel="noopener noreferrer">Python</BrowserLink> potrafi stworzy proste programy (gwnie w wierszu polece).
				Umiem podzieli kod na komponenty i funkcje, odczytywa dane z pliku tekstowego i zapisywa je, korzysta z importowanych paczek, stworzy prost gr itp.
				Lecz co chyba najwaniejsze, jestem w stanie napisa proste boty pobierajce dane ze stron internetowych (np. Amazon)!
			</ExpDescription>
		</>,
		office: <>
			<ExpImage hide={otherExp.hide} src={xls} />
			<ExpDescription hide={otherExp.hide}>
				Umiem tworzy arkusze i zarzdza informacjami w programie <BrowserLink href="https://pl.wikipedia.org/wiki/Microsoft_Excel" target="_blank" rel="noopener noreferrer">Microsoft Excel</BrowserLink>.
				Potrafi korzysta z gotowych formu, jak i tworzy wasne makra i funkcje w jzyku <BrowserLink href="https://pl.wikipedia.org/wiki/Visual_Basic_for_Applications" target="_blank" rel="noopener noreferrer">VBA</BrowserLink> (Visual Bacis for Applications).
				Jestem w stanie rwnie napisa proste pragramy w tym jzyku. 
			</ExpDescription>
			<ExpImage hide={otherExp.hide} src={ppt} />
			<ExpDescription hide={otherExp.hide}>
				Znam si na robieniu prezentacji w programie <BrowserLink href="https://pl.wikipedia.org/wiki/Microsoft_PowerPoint" target="_blank" rel="noopener noreferrer">Microsoft PowerPoint</BrowserLink>.
				Jako ucze, zawsze bardzo lubiem tworzy takie rzeczy i myl, e po czci wpyno to na moje obecne poczucie stylu.
				Potrafi stworzy prezentacj typu kiosk (automatyczn), podoy gos do slajdw, ustawi czas wywietlania slajdu, doda animacje i przede wszystkim sparwi, by cao prezentowaa si estetycznie.
			</ExpDescription>
			<ExpImage hide={otherExp.hide} src={doc} />
			<ExpDescription hide={otherExp.hide}>
				Potrafi rwnie tworzy proste dokumenty w programie <BrowserLink href="https://pl.wikipedia.org/wiki/Microsoft_Word" target="_blank" rel="noopener noreferrer">Microsoft Word</BrowserLink>.
				Umiem tworzy tabele, wykresy i tym podobne rzeczy.
			</ExpDescription>
		</>,
		cpp: <>
			<ExpImage hide={otherExp.hide} src={terminal} />
			<ExpDescription hide={otherExp.hide}>
				Jestem w stanie stworzy programy do odczytu i zapisu danych i posiadam wikszo z wczeniej wymienionych umiejtnoci (w sekcji o jzyku Python).
				Myl, e jest to fajna umiejtno dodatkowa, natomiast w jzyku <BrowserLink href="https://www.python.org" target="_blank" rel="noopener noreferrer">Python</BrowserLink> czuj si duzo bardziej komfortowo.
			</ExpDescription>
		</>
	}
}

export default contents